.main-advantages {
  padding-top: 100px;

  .container {
    display: flex;
    flex-direction: column;
    text-align: center;
    padding-bottom: 104px;
  }
}

.main-advantages__wrapper {
  order: 3;
}

.main-advantages__list {
  display: flex;
  list-style-type: none;
  padding: 0;
  margin: 0;
  gap: 30px;
}

.main-advantages__item {
  h3 {
    font-size: 24px;
    font-weight: 700;
    line-height: 36px;
    letter-spacing: 0em;
    text-align: center;
    margin: 15px 0 10px;
  }

  p {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: center;
    margin-top: 10px;
  }
}
